* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
  color: white;
  text-decoration: none;
}
.navbar {
  z-index: 1;
  position: fixed;
  width: 100%;
  line-height: 10vh;
  height: 8vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #c90501;
  color: white;
}
/* Modal Games */

.popup-overlay {
  background-color: rgba(60, 59, 59, 0.377);
}
.popup-content {
  border-radius: 5px;
  padding: 0.5rem;
  background-color: rgb(247, 247, 247);
  width: 90%;
  margin-right: 5%;
  margin-left: 5%;
}

html[data-theme="dark"] .popup-content {
  background-color: rgb(41, 45, 62);
  color: #fff;
  border: 1px solid #9a9595;
}

html[data-theme="light"] .popup-content {
  background-color: #fff;
  color: #000;
  border: 1px solid #d7d7d7;
}

html[data-theme="dark"] .popup-arrow > svg {
  color: rgb(41, 45, 62);
  stroke-width: 2px;
  stroke: #9a9595;
  stroke-dasharray: 30px;
  stroke-dashoffset: -54px;
}

html[data-theme="light"] .popup-arrow > svg {
  color: #fff;
  stroke-width: 2px;
  stroke: #d7d7d7;
  stroke-dasharray: 30px;
  stroke-dashoffset: -54px;
}

.close {
  float: right;
  border-radius: 20px;
  font-size: 25px;
  position: relative;
  top: 0;
  right: 0;
}
.logo {
  font-size: 20px;
  padding-left: 2vh;
}
.nav-links {
  display: flex;
  justify-content: flex-end;
  /* width: 75%; */
}
.container {
  max-width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-links li {
  font-size: 1.2rem;
  padding: 0 10px;
  margin: 0 10px;
  cursor: pointer;
  text-transform: capitalize;
  text-decoration: none;
}
Link {
  text-decoration: none;
}
button {
  border: none;
  background-color: none;
  font-size: 20px;
}
.Mobile-Menu-Button {
  display: none;
  background-color: #da0400;
  color: white;
}
hr {
  width: 80%;
  margin-left: 10%;
  display: none;
}
@media (max-width: 1100px) {
  p {
    font-size: 1rem;
  }
  .nav-links li {
    font-size: 15px;
  }
  hr {
    display: block;
  }

  .nav-links {
    display: none;
  }
  .nav-links-mobile {
    position: absolute;
    display: block;
    list-style: none;
    left: 0;
    top: 10%;
    transition: all 0.5s ease-in-out;
    color: white;
    width: 100%;
    background-color: #c90501;
  }
  ul li {
    width: 100%;
    transition: all 0.5s ease-in-out;
    text-align: center;
  }
  .Mobile-Menu-Button {
    position: absolute;
    display: block;
    right: 20px;
  }
}

