.loader {
  border: 10px solid #f3f3f3; /* Light grey */
  border-top: 10px solid #777777; /* Blue */
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
  margin: auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loaderdiv {
  margin: auto;
  width: 50%;
  margin-top: 55%;
  padding: 10px;
}

.navbar svg {
  stroke: currentcolor;
  fill: currentcolor;
  stroke-width: 0;
  height: 1.7rem;
  width: 1.7rem;
}

